.login{
    display: grid;
    place-items:center;
    height:100vh;
}
.login > button{
    width: 300px;
    background-color: #c6cacf;
    color: #eff2f5;
    font-weight: 800;
}
.login > button:hover{
    background-color: #eff2f5;
    color:#2e81f4;
    transition:ease-in-out;
    transition-delay: 12ms;
    
}
.login > div > img{
    object-fit: contain;
    height: 150px;
   }
.login > div{
    display:flex;
    flex-direction: column;
}