.story{
position: relative;
background-position: center center;
background-size: cover;
background-repeat: no-repeat;
width: 120px;
height: 200px;
box-shadow: 8px 5px 17px -7px rgba(0, 0, 0, 0.75);
border-radius: 10px;
margin-left: 10px;
transition: transform 100ms ease-in;
cursor: pointer;
}
.story:hover{
    transform: scale(1.07);
}
.story__avatar{
    margin:10px;
    border: solid 5px #2e81f4;
}
.story>h4{
    position:absolute;
    bottom: 20px;
    left:20px;
    color:white;
}
